.search-box {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;

  .search {
    width: 50%;
    height: 40px;
    padding: 1rem;
    border: 1px solid black;
  }
}
@media screen and (max-width: 425px) {
  .search {
    width: 100% !important;
  }
}
